:root {
  --primary-red: #ff2442;
  --primary-blue: #3db2ff;
  --neutral-bg: #ffedda;
  --secondary: #ffb830;
}

/*
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
/*
  2. Remove default margin
*/
* {
  margin: 0;
}
/*
  3. Allow percentage-based heights in the application
*/
html,
body {
  height: 100%;
}
/*
  Typographic tweaks!
  4. Add accessible line-height
  5. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url("img/bg.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
/*
  6. Improve media defaults
*/
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
/*
  7. Remove built-in form typography styles
*/
input,
button,
textarea,
select {
  font: inherit;
}
/*
  8. Avoid text overflows
*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
/*
  9. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}

.wrapper {
  background-color: teal;
  padding: 2rem;
  width: 12rem;
  height: 12rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.rectangle {
  position: absolute;
  width: 10rem;
  height: 1rem;
}
.red {
  background-color: red;
  transform-origin: top;
}
.blue {
  background-color: blue;
  transform-origin: bottom;
}

.visible {
  z-index: 1;
}

.hidden {
  z-index: 2;
}
.container {
  position: absolute;
  display: flex;
  height: 40rem;
  width: 40rem;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  top: 3rem;
}

.letter-container {
  position: absolute;
  display: flex;
  height: 40rem;
  width: 40rem;
  justify-content: center;
  align-items: center;
  top: 3rem;
}

.card-letter {
  background-image: url("img/card-letter.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  height: 35rem;
  width: 30rem;
  left: 40%;
  z-index: 9;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.letter-content {
